}

.marker-cluster {
    background-clip: padding-box;
    border-radius: 20px;
}

.marker-cluster div {
    width: 30px;
    height: 30px;
    margin-left: 5px;
    margin-top: 5px;
    text-align: center;
    border-radius: 15px;
    font: 12px "Helvetica Neue", Arial, Helvetica, sans-serif;
}

.marker-cluster span {
    line-height: 30px;
}

svg{
    margin: auto;
}

.leaflet-control-command-interior, .leaflet-popup-content-wrapper{
    border-radius: 0px;
}

.leaflet-zoom-anim .leaflet-zoom-animated {
	will-change: auto;
}

.leaflet-control-command-interior.show{
    display: block;
}
.easy-button-button{
    display: none;
}
.mobile{display: none;}
@media only screen and (max-width: 600px) {
    .leaflet-control-command-interior{
        display: block;
    }
    .leaflet-right .leaflet-control .no-margin ,.leaflet-bottom .leaflet-control{
        margin: 0px;
    }
    .leaflet-control-command-interior{
        width: 100vw;
        max-width: 100vw;
    }
    .mobile{display: block;}
    
    .full{
        display: none;
    }
    .leaflet-control-attribution.show{width:100vw;background-color: white;padding: unset;}
    
    #radioDiv{
        padding-bottom: .5em;
    }
}
.leaflet-control-attribution{display: none;}
.leaflet-control-attribution.show{display: block;}

#btnAtt{
    position: absolute;
    right: 5px;
    top: 5px;
    padding: unset;
    text-align: center;
    vertical-align: bottom;
    padding: 2px;
}
